.account {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid black;
  background-color: #fff;
  width: 80%;
  margin: 0 auto;
  flex-direction: column;
  padding: 1.5rem;
  box-sizing: border-box;
  text-align: left;
  margin-bottom: 2rem;
}

.account_amount {
  margin: 0;
  font-size: 2.5rem;
  font-weight: bold;
}

.account_amount_description {
  margin: 0;
}

.account_title {
  margin: 0;
  padding: 0;
  font-size: 1rem;
  font-weight: normal;
}

.account_content_wrapper {
  width: 100%;
  flex: 1;
}

.edit_button {
  border-color: #00bc77;
  background-color: #00bc77;
  color: #fff;
  font-weight: bold;
  padding: 10px;
}

.header {
  color: #fff;
  margin-bottom: 2rem;
}

.transaction_button {
  display: block;
  width: 100%;
  padding: 8px;
  font-size: 1.1rem;
  font-weight: bold;
  margin-top: 1rem;
  border-color: #00bc77;
  background-color: #00bc77;
  color: #fff;
}

@media (min-width: 720px) {
  .account {
    flex-direction: row;
  }

  .account_content_wrapper.cta {
    flex: 0;
  }

  .transaction_button {
    width: 200px;
  }
}

form.updateForm {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 50%;
  margin: auto;
  align-items: center;
  justify-content: center;
  align-content: center;
}

@media (max-width: 720px) {
  form.updateForm {
    width: 100%;
  }
}

form.updateForm p {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  align-content: center;
}

form.updateForm label {
  display: none;
}

form.updateForm input {
  height: 25px;
  width: 40%;
  margin: 0px 10px;
  font-weight: 700;
  padding: 15px;
  font-size: 1.1rem;
}

form.updateForm button {
  height: 40px;
  width: 22%;
  margin: 0px 10px;
  border-color: #00bc77;
  background-color: #00bc77;
  color: #fff;
  font-size: 1.1rem;
  font-weight: bold;
}